#questionDiv {
    /*display: inline-block;*/
    /*padding: 100px;*/
    /*font-size: 20px;*/
    animation: fadein 0.7s ease-in-out;
    margin-bottom: 20px;
}


#gameDiv {
    /*display: inline-block;*/
    /*padding: 100px;*/
    /*font-size: 20px;*/
    animation: fadein 0.7s ease-in-out;
    margin-bottom: 20px;
    display: flow;
    align-content: center;
    justify-content: center;
    justify-items: center;
}

@keyframes fadein {
    from {
        opacity: 0;
        transform: translateX(100px);
    }
    to {
        opacity: 1;
        transform: none;
    }
}

@-webkit-keyframes fadein {
    /* Safari and Chrome */
    from {
        opacity: 0;
        transform: translateX(100px);
    }
    to {
        opacity: 1;
        transform: none;
    }
}


